export default class GoogleAnalytics extends akamai.amp.Plugin {
  constructor(player, config) {
    super(player, config)

    ga('create', this.config.trackingId, 'auto')
    ga('send', 'pageview')
  }

  generateGoogleAnalyticsVO(eventName, type = "video") {
    if (eventName == null)
      return

    const events = this.config.events[type]
    if (events == null)
      return

    const event = events.filter((event) => (event != null && event.type == eventName))[0]
    if (event == null)
      return

    const data = event.data || event.detail
    if (data == null)
      return

    const vo = {
      trackingid: this.config.trackingId
    }

    for (let key in data) {
      let value = this.player.evaluateBindings(data[key])
      if (key === "customDimension") {
        let cdim = new Object()
        for (let dim in value) {
          let dimValue = value[dim]
          if (dimValue === "viewDuration")
            dimValue = this.viewDuration
          ga("set", dim, dimValue)
          cdim[dim] = dimValue
        }
        value = cdim
      }

      vo[key.toLowerCase()] = value
    }

    return vo
  }

  trackEvent(eventName, type = "video") {
    const obj = this.generateGoogleAnalyticsVO(eventName, type)
    if (obj == null)
      return

    ga("send", "event", obj.eventcategory, obj.eventaction, obj.eventlabel, obj.eventvalue)

    if (obj.customdimension == null)
      return

    for (let dim in obj.customdimension) {
      // TODO: Why is this being set to null? "set" is called in the VO function with a non-null value
      ga("set", dim, null)
    }
  }

  onready() {
    this.bindHandlers(["onadstarted", "onadended"])
    this.player.addEventListener("adstarted", this.onadstarted)
    this.player.addEventListener("adended", this.onadended)
  }

  onstarted() {
    this.started = true
    this.currentTime = 0
    this.viewDuration = 0
    this.trackEvent("started")
  }

  onmediachange() {
    this.onended()
  }

  onended() {
    if (this.started === false)
      return

    this.started = false
    this.trackEvent("ended")
  }

  ontimeupdate(event) {
    const currentTime = Math.round(event.detail)
    if (currentTime > this.currentTime) {
      this.viewDuration++
      this.currentTime = currentTime
    }
  }

  onadstarted(event) {
    this.trackEvent("started", "ads")
  }

  onadended(adVO) {
    this.trackEvent("ended", "ads")
  }
}
